import { Component } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/observable/from';
import 'rxjs/add/observable/interval';
import 'rxjs/add/observable/onErrorResumeNext';
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/zip';

declare function escape(s: string): string;

interface Monography {
  title: string;
  id: string;
  content: string;
  title_search: string;
}

@Component({
  selector: 'app-root',
  template: require('./app.component.html'),
  styles: [require('./app.component.css')]
})
export class AppComponent {
  result: Array<Monography> = [];
  monography: Monography;
  title: string = 'app works!';

  private db;

  constructor(private http: Http) {
    let openDatabaseRequest = indexedDB.open("monography");
    openDatabaseRequest.onsuccess = () => {
      this.db = openDatabaseRequest.result;
      let monographyStore = this.db.transaction(["monographies"], "readonly").objectStore('monographies');
      let count = monographyStore.count();
      count.onsuccess = () => console.log(count.result);
    }
    openDatabaseRequest.onupgradeneeded = () => {
      let store = openDatabaseRequest.result.createObjectStore("monographies", { keyPath: 'id' });
      store.createIndex('by_title', "title_search", { unique: false });
    }
  }

  addMonography(monography: Monography, callback?: () => void) {
    let transaction = this.db.transaction('monographies', 'readwrite');
    let monographyStore = transaction.objectStore('monographies');
    let request = monographyStore.put(monography);
    transaction.oncomplete = () => callback();
    transaction.onerror = () => {
      console.error("error : " + transaction.error);
    }
  }

  getMonography(id: string, callback: (result: Monography) => void) {


    let monographyStore = this.db.transaction(["monographies"], "readonly").objectStore('monographies');
    let query = monographyStore.get(id);
    query.onsuccess = () => {
      let monography = query.result;
      callback(monography);
    }
    query.onerror = () => {
      console.error("Read product error : " + query.error);
    }
  }

  strip(html: string): string {
    let tmp = document.createElement("DIV");
    tmp.innerHTML = html.replace(/<(\/?img)/g, "<$1_none");
    return tmp.textContent || tmp.innerText || "";
  }

  public search(text: string) {
    this.result = [];
    if (!text)
      return;
    let range = IDBKeyRange.bound(text.toLowerCase(), text.toLowerCase() + '\uffff');

    let monographyStore = this.db.transaction(["monographies"], "readonly").objectStore('monographies');
    let request = monographyStore.index("by_title").openCursor(range);
    request.onsuccess = (r) => {
      let cursor = request.result
      if (cursor) {
        if (cursor.value)
          this.result.push(cursor.value);

        if (this.result.length < 30)
          cursor.continue();
        else
          cursor.advance(100000);
      }
    }
  }

  public selectMonography(monography: Monography) {
    this.monography = monography;
  }


  public reload() {
    let i = 0;
    let tick = new Subject();
    this.http.get("http://localhost:8000/_list.json")
      .mergeMap(res => {
        let list = res.json() as Array<string>;
        let s: Subject<string> = new Subject();
        list.forEach(id => {
          this.getMonography(id, (mono) => {
            if (!mono) {
              s.next(id);
            }
            else {
              console.log("here:", id);
            }
          });
        });
        return s;
      })
      .zip(tick)
      .map(it => it[0])
      .mergeMap(file => Observable.onErrorResumeNext(this.http.get("http://localhost:8000/html/" + file)))
      .subscribe((res) => {
        let id = res.url.substring(res.url.lastIndexOf('/') + 1);
        let raw = res.text().match(/<a name="nom"><\/a><h2 align="center">(.*?)<\/h2>/)
        if (!raw) return tick.next();
        let title = this.strip(raw[1]);
        if (!title) return tick.next();
        title=title.trim();
        let content = res.text().replace(/[\s\S]*<b>Médicaments<\/b><\/font><br>/, "").replace(/<\/body[\s\S]*/, "");
        let mono = { title: title, id: id, content: content, title_search: title.toLowerCase() };
        this.addMonography(mono, () => {console.log('added', id); tick.next()} );
        i++;
        console.log(i);
      });
    tick.next();
  }
}



